export default {
	data() {
		return {
			list: [],
			loading: false,
			finish: false,
			page: 1
		};
	},

	created() {
		this.list = this.rdList();
	},

	onReachBottom() {
		this.append();
	},

	methods: {
		append() {
			this.loading = true;

			if (this.page > 3) {
				this.finish = true;
			} else {
				this.page += 1;

				setTimeout(() => {
					this.$refs['waterfall'].append(this.rdList());
					this.loading = false;
				}, 1500);
			}
		},

		rdList() {
			return new Array(10).fill(1).map(() => {
				return parseInt(Math.random() * 10) + 1;
			});
		}
	}
};
